const _baseUrl = process.env.API_ROOT
export default {
    getRankAll() {
        return _baseUrl + '?ctl=rankh5&act=rank_all'
    },
    getRankAnchorLastHour() {
        return _baseUrl + '?ctl=rankh5&act=rank_anchor_last_hour'
    },
    doFollow(){
        return _baseUrl + '?ctl=user&act=follow'
    },
    getRankList(listType) {
        return _baseUrl + '?ctl=rankh5&act='+listType
    },
}


