<template>
	<header class="header-bar">
		<a class="pull-left back" @click="back"></a>
	  	<h1 class="title" v-text="pageTitle"></h1>
	</header>
</template>
<script>
	export default{
		props: ['pageTitle'],
		methods: {
			back(){
				this.$router.go(-1);
			}
		}
	}
</script>
<style lang="less" scoped>
	@import "../assets/less/variable.less";
	.header-bar {
	  	position: absolute;
	    right: 0;
	    left: 0;
	    z-index: 10;
	    height: 2.2rem;
	    padding-right: 0.5rem;
	    padding-left: 0.5rem;
	    background-color: #f7f7f8;
	    -webkit-backface-visibility: hidden;
	    backface-visibility: hidden;
	    background-color: #fff;
	    .title {
		    position: absolute;
		    top:0;
		    display: block;
		    width: 100%;
		    padding: 0;
		    margin: 0 -0.5rem;
		    font-size: 0.75rem;
		    font-weight: 500;
		    line-height: 2.2rem;
		    color: #5a5a5a;
		    text-align: center;
		    white-space: nowrap;
		}
		.pull-left{
			&:before{
				font-family: 'iconfont';
				content: '\e601';
				color: @color-theme;
			}
			position: relative;
		    top: 0.35rem;
		    z-index: 20;
		    margin-top: 0;
		    font-weight: 400;
		    top: 0;
		    padding: 0;
		    font-size: 0.8rem;
		    line-height: 2.2rem;
		    height: 2.2rem;
		    color: #0894ec;
		    border: 0;
		}
		&:after {
		    content: '';
		    position: absolute;
		    left: -50%;
		    bottom: 0;
		    right: auto;
		    top: auto;
		    height: 1px;
		    width: 200%;
		    background-color: #c9c9c9;
		    display: block;
		    z-index: 15;
		    -webkit-transform-origin: 50% 100%;
		    transform-origin: 50% 100%;
		    transform: scale(0.5, 0.5);
		}
	}
</style>


