import Vue from 'vue'
import Router from 'vue-router'
import PreHourRank from '@/components/PreHourRank'
import RankList from '@/views/dz_zhll/RankList'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'RankList',
      component: RankList
    },
 	  {
      path: '/RankList',
      name: 'RankList',
      component: RankList
    },
    {
      path: '/PreHourRank',
      name: 'PreHourRank',
      component: PreHourRank
    }
  ]
})


