<template>
	<router-link :to="{path: '/PreHourRank'}" class="list-top" :style="bgListTopBar">
		<p>上一小时榜单</p>
		<div class="prehour">
			<div class="prehour-img" v-for="item in lastHour">
				<img :src="item.head_image" alt="">
			</div>
		</div>
	</router-link>
</template>
<script>
	export default{
		props: ['lastHour'],
		data(){
			return{
				bgListTopBar: {
		          	backgroundImage: 'url(' + require('../assets/img/icon_right.png') + ')'
		        }
			}
		}
	}
</script>
<style lang="less" scoped>
	@import "../assets/less/variable.less";
	.list-top{
		padding: 0.9rem 0.75rem;
		padding-right: 1.5rem;
		padding-left: 0;
		margin-left: 0.75rem;
		border-bottom: 1px solid @bdcolor;
		display: flex;
	    justify-content: space-between;
	    align-items: center;
	    background-repeat: no-repeat;
	    background-size: 0.7rem;
        background-position: 97% center;
	    background-position: -webkit-calc(96.5%) center;
	    background-position: calc(96.5%) center;
	    .prehour{
	    	display: flex;
	    	height: 2rem;
	    	.prehour-img{
		    	width: 2rem;
	    		height: 2rem;
	    		overflow: hidden;
	    		border-radius: 50px;
	    		margin-right: 0.5rem;
	    		flex-shrink:0;
	    		img{
	    			width: 100%;
	    			height: 100%;
	    		}
    		}
	    }
	}
</style>


