<template>
	<div class="rank-container">
	    	<!-- 渐变层 -->
	    	<div class="gradual" :style="{background: gradualColor}" v-if="!isNullData"></div>
			<div class="rank-hd" id="rank-hd" :style="{ backgroundColor: bgRank,opacity:opacity}">
				<img :src="rankImg" class="rank-img" :class="{animation}">
			</div>
			<div class="rank-bd">
				<mt-loadmore :top-method="loadTop" ref="loadmore">
					<div class="swiper-container" :style="{height: swiperContainerHeight+'px'}">
					  	<div class="swiper-wrapper">
						    <div class="swiper-slide">
						    	<!-- 上一小时榜单 -->
						    	<pre-hour-rank-bar :lastHour="lastHour"></pre-hour-rank-bar>
						    	<!-- 播主小时榜 -->
						    	<item-rank-list :rankList="anchorHour" isHourRank="true"></item-rank-list>
						    </div>
						    <div class="swiper-slide">
						    	<!-- 播主日榜 -->
						    	<item-rank-list :rankList="anchorDay" isHourRank="false"></item-rank-list>
						    </div>
						    <div class="swiper-slide">
						    	<!-- 播主周榜 -->
						    	<item-rank-list :rankList="anchorWeek" isHourRank="false"></item-rank-list>
						    </div>
						    <div class="swiper-slide">
						    	<!-- 用户日榜 -->
						    	<item-rank-list :rankList="userDay" isHourRank="false"></item-rank-list>
						    </div>
						    <div class="swiper-slide">
						    	<!-- 用户周榜 -->
						    	<item-rank-list :rankList="userWeek" isHourRank="false"></item-rank-list>
						    </div>
					  	</div>
					</div>
				</mt-loadmore>
			</div>
			<!-- 导航分页器 -->
			<div class="rank-nav float-rank-nav" v-if="showMiniNav">
				<ul class="rank-nav-main swiper-pagination"></ul>
			</div>
			<div class="rank-nav" v-else>
				<ul class="rank-nav-main swiper-pagination" :style="{transform: 'translate3d(-'+navItemWith+'px,0,0)'}"></ul>
			</div>
	</div>
</template>
<script>
	import Vue from 'vue'
	import { Toast, Loadmore } from 'mint-ui'
	import axios from 'axios'
	import api from '@/config/api';
	import Swiper from '../../../static/js/swiper/swiper-3.4.2.min.js'
	import ItemRankList from '@/components/ItemRankList'
	import PreHourRankBar from '@/components/PreHourRankBar'
	require('../../../static/js/swiper/swiper-3.4.2.min.css')
	Vue.component(Loadmore.name, Loadmore)
	export default{
		components: {
			ItemRankList,
			PreHourRankBar
		},
		data(){
			return {
				bgRank: '#bf6ee0',
				gradualColor: '',
				rankImg: '',
				animation: 'animation',
				navItemWithSets: [],
				navItemWith: 0,
				allNavItemWith: 20,
				opacity: 1,
				swiperContainerHeight: null,
				showMiniNav: false,
				listTopHeight: 0,
				NullDataHeight: 0,
				isNullData: false,
				minSwiperContainerHeight: 0,
				anchorHour: [],
				anchorDay: [],
				anchorWeek: [],
				userDay: [],
				userWeek: [],
				lastHour: [],
				onTopLoaded: false,
				listType: "rank_anchor_hour"
			}
		},
		created(){
   			this.getData();
   			this.onScoll();
   			document.title = '排行榜';
   		},
   		mounted () {
   			// 滑动列表最小高度
 			this.minSwiperContainerHeight = screen.height - document.querySelector(".rank-hd").offsetHeight - 114;
 			App.hidenOrShowNavBar(false);
   		},
   		methods:{
   			loadTop(id){
   				// 下拉刷新加载
   				var self = this;
              	self.getListData(self.listType);
              	if(self.onTopLoaded){
              		setTimeout(function(){
	                  	self.$refs.loadmore.onTopLoaded(id);
	              	}, 500);
	              	self.onTopLoaded = false;
              	}
          	},
   			getNavItemWithSets(){
   				// 获取滑动分页器每项的宽度
   				let self = this, withSets=[];

    	 		$(".nav-item").each(function(){
			   		withSets.push($(this).width());
			   		self.allNavItemWith += $(this).width();
			  	});
			  	return withSets;
   			},
   			getData(is_refash){
   				// 获取服务端数据
   				let self = this;
   				axios.get(api.getRankAll())
			      	.then(res => {
			      		document.querySelectorAll(".null-data")[0] && (document.querySelectorAll(".null-data")[0].style.display = "block");
			      		let data = res.data;
			      		if(data.status == 1){
				      		self.anchorHour = data.anchor && data.anchor.hour || [];
		      				self.anchorDay = data.anchor && data.anchor.day || [];
		      				self.anchorWeek = data.anchor && data.anchor.week || [];
		      				self.lastHour = data.anchor && data.anchor.last_hour || [];
		      				self.userDay = data.user && data.user.day || []
		      				self.userWeek = data.user && data.user.week || [];

		      				console.log(self.lastHour);

			      		}
			      		else{
			      			Toast('请求出错！');
			      		}
			      		
	      				self.$nextTick(function(){
	      					self.onTopLoaded = true;
	      					!is_refash && self.init();
	      				});
			      	})
			      	.catch(err => console.log(err))
   			},
   			getListData(ListType){
   				// 获取单个列表数据（下拉刷新）
   				let self = this;
   				axios.get(api.getRankList(ListType))
			      	.then(res => {
			      		document.querySelectorAll(".null-data")[0] && (document.querySelectorAll(".null-data")[0].style.display = "block");
			      		let data = res.data;
			      		if(data.status == 1){
			      			switch(ListType){
			      				case 'rank_anchor_hour':
	      					 		self.anchorHour = data.items || [];
	      					 		self.lastHour = data.last_items || [];
	      					 		self.swiperContainerHeight = $(".swiper-slide-active").find(".rank-list").height();
	      					 		self.$nextTick(function(){
	      					 			self.getSwiperContainerHeight(self.anchorHour[0], true);
	      					 		});
	      					 		break;
	      					 	case 'rank_anchor_day':
	      					 		self.anchorDay = data.items || [];
	      					 		self.swiperContainerHeight = $(".swiper-slide-active").find(".rank-list").height();
	      					 		self.$nextTick(function(){
	      					 			self.getSwiperContainerHeight(self.anchorDay[0]);
	      					 		});
	      					 		break;
	      					 	case 'rank_anchor_week':
	      					 		self.anchorWeek = data.items || [];
	      					 		self.swiperContainerHeight = $(".swiper-slide-active").find(".rank-list").height();
	      					 		self.$nextTick(function(){
	      					 			self.getSwiperContainerHeight(self.anchorWeek[0]);
	      					 		});
	      					 		break;
	      					 	case 'rank_anchor_week':
	      					 		self.userDay = data.items || [];
	      					 		self.swiperContainerHeight = $(".swiper-slide-active").find(".rank-list").height();
	      					 		self.$nextTick(function(){
	      					 			self.getSwiperContainerHeight(self.userDay[0]);
	      					 		});
	      					 		break;
	      					 	case 'rank_user_week':
	      					 		self.userWeek = data.items || [];
	      					 		self.swiperContainerHeight = $(".swiper-slide-active").find(".rank-list").height();
	      					 		self.$nextTick(function(){
	      					 			self.getSwiperContainerHeight(self.userWeek[0]);
	      					 		});
	      					 		break;
			      			}
			      		}
			      		else{
			      			Toast('请求出错！');
			      		}
			      		
	      				self.$nextTick(function(){
	      					self.onTopLoaded = true;
	      				});
			      	})
			      	.catch(err => console.log(err))
   			},
   			init(){
   				// 初始化滑动切换
   				let self = this;
	   			self.rankImg = self.anchorHour[0] && self.anchorHour[0].head_image;	
	   			let mySwiper = new Swiper('.swiper-container', {
		   			pagination : '.swiper-pagination',
		   			paginationClickable: true,
		   			initialSlide: 0,
		   			loop: true,
		   			autoplay: 0,
		   			onSlideChangeStart: function(swiper){
		   				let activeIndex = swiper.activeIndex,
		   					$swiperSlideActive = $(".swiper-slide-active"),
		   					$nullData = $swiperSlideActive.find(".null-data"),
		   					$listTop = $swiperSlideActive.find(".list-top"),
		   					$rankList = $swiperSlideActive.find(".rank-list");
					    self.animation = '';
				      	self.opacity = 0.5;
				      	! self.listTopHeight && (self.listTopHeight = $listTop.height());
				      	! self.NullDataHeight && $nullData.length && (self.NullDataHeight = $nullData.height());
				      	self.swiperContainerHeight = $rankList.height();
					    setTimeout(function(){
				    	 	switch(activeIndex){
		      					case 1:
		      						self.navItemWith = 0;
		      						self.bgRank = '#9b4ccd';
		      						self.rankImg = self.anchorHour[0] && self.anchorHour[0].head_image;
		      						self.getSwiperContainerHeight(self.anchorHour[0], true);
		      						self.isNullData = ! self.anchorHour[0] ? true : false;
		      						self.listType = 'rank_anchor_hour';
		      						break;
		      					case 6:
		      						self.navItemWith = 0;
		      						self.bgRank = '#bf6ee0';
		      						self.rankImg = self.anchorHour[0] && self.anchorHour[0].head_image;
		      						self.getSwiperContainerHeight(self.anchorHour[0], true);
		      						self.isNullData = ! self.anchorHour[0] ? true : false;
		      						self.listType = 'rank_anchor_hour';
		      						break;
		      					case 2:
		      						self.navItemWith = self.navItemWithSets[0]+20;
		      						self.bgRank = '#d499e5';
		      						self.rankImg = self.anchorDay[0] && self.anchorDay[0].head_image;
		      						self.getSwiperContainerHeight(self.anchorDay[0]);
		      						self.isNullData = ! self.anchorDay[0] ? true : false;
		      						self.listType = 'rank_anchor_day';
		      						break;
		      					case 3:
		      						self.navItemWith = self.navItemWithSets[0]+self.navItemWithSets[1]+40;
		      						self.bgRank = '#54d5ab';
		      						self.rankImg = self.anchorWeek[0] && self.anchorWeek[0].head_image;
		      						self.getSwiperContainerHeight(self.anchorWeek[0]);
		      						self.isNullData = ! self.anchorWeek[0] ? true : false;
		      						self.listType = 'rank_anchor_week';
		      						break;
		      					case 4:
		      						self.navItemWith = self.navItemWithSets[0]+self.navItemWithSets[1]+self.navItemWithSets[2]+60;
		      						self.bgRank = '#5f85df';
		      						self.rankImg = self.userDay[0] && self.userDay[0].head_image;
		      						self.getSwiperContainerHeight(self.userDay[0]);
		      						self.isNullData = ! self.userDay[0] ? true : false;
		      						self.listType = 'rank_user_day';
		      						break;
		      					case 5:
		      						self.navItemWith = self.navItemWithSets[0]+self.navItemWithSets[1]+self.navItemWithSets[2]+self.navItemWithSets[3]+80;
		      						self.bgRank = '#cd983f';
		      						self.rankImg = self.userWeek[0] && self.userWeek[0].head_image;
		      						self.getSwiperContainerHeight(self.userWeek[0]);
		      						self.isNullData = ! self.userWeek[0] ? true : false;
		      						self.listType = 'rank_user_week';
		      						break;
		      					case 0:
		      						self.navItemWith = self.navItemWithSets[0]+self.navItemWithSets[1]+self.navItemWithSets[2]+self.navItemWithSets[3]+80;
		      						self.bgRank = '#cd983f';
		      						self.rankImg = self.userWeek[0] && self.userWeek[0].head_image;
		      						self.getSwiperContainerHeight(self.userWeek[0]);
		      						self.isNullData = ! self.userWeek[0] ? true : false;
		      						self.listType = 'rank_user_week';
		      						break;
		      				}
					    }, 100);
					    $(".swiper-pagination-bullet-active").find(".sub-item-title").removeClass("animationed");
					},
					onSlideChangeEnd: function(swiper){
						let activeIndex = swiper.activeIndex;
						self.animation = 'animation';
						self.opacity = 1;
						$(".swiper-pagination-bullet-active").find(".sub-item-title").addClass("animationed");
	    			},
		   			paginationBulletRender: function (swiper, index, className) {
		   				switch(index){
		   					case 0:
		   						return '<li class="nav-item '+className+'"><span class="item-title">播主小时榜</span><span class="sub-item-title animationed">每小时更新</span></li>';
		   						break;
		   					case 1:
		   						return '<li class="nav-item '+className+'"><span class="item-title">播主日榜</span><span class="sub-item-title">每日更新</span></li>';
		   						break;
		   					case 2:
		   						return '<li class="nav-item '+className+'"><span class="item-title">播主周榜</span><span class="sub-item-title">每周更新</span></li>';
		   						break;
		   					case 3:
		   						return '<li class="nav-item '+className+'"><span class="item-title">用户日榜</span><span class="sub-item-title">每日更新</span></li>';
		   						break;
		   					case 4:
		   						return '<li class="nav-item '+className+'"><span class="item-title">用户周榜</span><span class="sub-item-title">每周更新</span></li>';
		   						break;
		   				}
				  	}
		 		})
			  	this.navItemWithSets = this.getNavItemWithSets();
   			},
   			onScoll(){
   				let self = this;
   				window.onscroll=function(){
   					document.body.scrollTop >= document.querySelector('.rank-hd').clientHeight ? self.showMiniNav = true : self.showMiniNav = false;
   				}
   			},
   			getSwiperContainerHeight(data, hasListTop=false){
   				// 获取滑动列表实际高度
				data && (this.swiperContainerHeight = this.minSwiperContainerHeight > this.swiperContainerHeight + this.listTopHeight ? this.minSwiperContainerHeight : hasListTop ? this.swiperContainerHeight + this.listTopHeight : this.swiperContainerHeight) || (this.swiperContainerHeight = this.minSwiperContainerHeight > this.listTopHeight + this.NullDataHeight ? this.minSwiperContainerHeight : this.listTopHeight + this.NullDataHeight);
   			}
   		},
   		watch:{
   			bgRank(){
   				this.gradualColor = 'linear-gradient(to bottom, '+this.bgRank+' 0%,#ffffff 100%)';
   			}
   		}
	}
</script>
<style lang="less">
	@import "../../assets/less/variable.less";
	@keyframes myfirst
	{
		from {margin-top: 0;}
		to {margin-top: -10%;}
	}
	@keyframes subbottom
	{
		from {bottom: -0.75rem;}
		to {bottom: 0.6rem;}
	}
	.rank-container{
		background: #fff;
	}
	.gradual{
		position: absolute;
		left: 0;
		right: 0;
		pointer-events: none;
		height: 229px;
	    // background: linear-gradient(to bottom, #bf6ee0 0%,#ffffff 100%);
	}
	.rank-hd{
		position: relative;
		display: flex;
		align-items: flex-end;
		height: 7.6rem;
		background-size: 100%;
		overflow: hidden;
		transition: all 1s;
		.rank-img{
			position: absolute;
			top: 0;
			left: 0;
			width: 100%;
			opacity: 0.15;
			margin-top: 0;
		}
		.animation{
			animation:myfirst 2s;
			animation-fill-mode: forwards;
		}
		
	}
	.swiper-pagination{
		position: initial !important;
		text-align: left !important;
		.swiper-pagination-bullet-active{
			background: transparent;
		}
		.swiper-pagination-bullet{
			border-radius: 0;
			background: transparent;
			opacity:1;
		}
	}
	.rank-nav{
		position: absolute;
		top: 4.75rem;
		left: 0;
		width: 100%;
		overflow: hidden;
		padding: 0 0.5rem;
		.rank-nav-main{
			width: 200%;
		}
		.nav-item{
			margin:0 10px;
			width: initial;
			height: 2.5rem;
			line-height: 1;
			display: inline-table;
			position: relative;
			span{
				display: block;
				color: #fff;
				opacity: 0.6;
			}
			.item-title{
				font-size: 0.9rem;
				line-height: 1.1;
			}
			.sub-item-title{
				position: absolute;
				display: none;
				font-size: 0.6rem;
				bottom: -0.75rem;
				&.animationed{
					animation: subbottom 0.3s;
					animation-fill-mode: forwards;
				}
			}
			&.swiper-pagination-bullet-active{
				.item-title{
					font-weight: 700;
					opacity: 1;
				}
				.sub-item-title{
					display: block;
				}
			}
		}
		&.float-rank-nav{
			position: fixed;
			top: 0;
			left: 0;
			z-index: 99999999;
			padding: 0;
			margin:0;
			width: 100%;
			border-bottom: 1px solid @bdcolor;
			.rank-nav-main{
				display: flex;
				flex-shrink: 1;
				width: 100%;
				justify-content:space-around;
				background:#fff;
				padding: 0 0.5rem;
				-webkit-tap-highlight-color:rgba(255,255,255,0);

				.nav-item{
					height: 2.2rem;
					line-height: 2.2rem;
					margin:0;
					span{
						color:#333;
					}
					.item-title{
						font-size: 0.65rem;
						padding: 0;
					}
					.sub-item-title{
						display:none;
					}
				}
				.swiper-pagination-bullet-active{
					.item-title{
						color: @color-theme;
						position: relative;
						&:before{
							display: block;
							content: '';
							position: absolute;
							bottom: 0;
							left: 0;
							width: 100%;
							height: 2px;
							background: @color-theme;
						}
					}
				}
			}
		}
	}
	.rank-nav .swiper-pagination-clickable .swiper-pagination-bullet{
		cursor: none;
	}
	@media only screen and (max-width: 320px){
	    .rank-nav.float-rank-nav .rank-nav-main .nav-item .item-title{
	    	font-size: 0.6rem;
	    }
	}
</style>


