<template>
	<div v-cloak>
		<header-bar :pageTitle="pageTitle"></header-bar>
		<div class="content">
			<div class="prehour-list" v-if="rankList.length">
				<div class="list-list" v-for="(item, index) in rankList">
					<template v-if="index <= 2">
						<div class="rank-numb rank-one" v-show="(index == 0)" :class="[item.rank == 'up' ? 'rank-up' : 'rank-down']">{{index+1}}</div>
						<div class="rank-numb rank-two" v-show="(index == 1)" :class="[item.rank == 'up' ? 'rank-up' : 'rank-down']">{{index+1}}</div>
						<div class="rank-numb rank-three" v-show="(index == 2)" :class="[item.rank == 'up' ? 'rank-up' : 'rank-down']">{{index+1}}</div>
					</template>
					<template v-else>
						<div class="rank-numb" :class="[item.rank == 'up' ? 'rank-up' : 'rank-down']">{{index+1}}</div>
					</template>
					<div class="anchor-img">
						<img class="anchor-image" :src="item.head_image" alt="">
						<div class="rank-icon" v-if="index <= 2">
							<img src="../assets/img/rank-one.png" alt="" class="rank-icon-img" v-show="(index == 0)">
							<img src="../assets/img/rank-two.png" alt="" class="rank-icon-img" v-show="(index == 1)">
							<img src="../assets/img/rank-three.png" alt="" class="rank-icon-img" v-show="(index == 2)">
						</div>
					</div>
					<div class="anchor" @click="viewUser(item)">
						
						<div class="anchor-info">
							<div class="anchor-name" v-text="item.nick_name"></div>
							<div class="hot-numb">映票值：{{item.ticket}}</div>
						</div>
					</div>
					<div class="state">
						<template v-if="item.live_in == 1">
							<span class="state-live" @click="enterRoom(item)">LIVE</span>	
						</template>
						<template v-else>
							<span class="state-concern" v-if="item.has_focus">已关注</span>
							<span class="state-follow" v-else @click="follow(item)">关注</span>
						</template>
					</div>
				</div>
			</div>
			<div class="null-data" v-else>
				<span>暂无数据</span>
			</div>
		</div>
	</div>
</template>
<script>
	import headerBar from '@/components/headerBar'
	import axios from 'axios'
	import api from '@/config/api'
	import { Toast , Indicator } from 'mint-ui'

	export default{
		components: {
			headerBar
		},
		data(){
			return{
				rankList: [],
				pageTitle: '上一小时榜单'
			}
		},
		created(){
			this.getData();
		    document.title = '排行榜';
		},
		mounted(){
			window.onscroll = null;
		    App.hidenOrShowNavBar(true);
		},
		methods: {
			getData(){
				Indicator.open('加载中...');
				let self = this;
				axios.get(api.getRankAnchorLastHour())
			      	.then(res => {
			      		Indicator.close();
			      		let data = res.data;
			      		if(data.status == 1){
			      			self.rankList = data.items;
			      		}
			      		else{
			      			Toast(data.error || '请求数据出错');
			      		}
			      	})
			      	.catch(err => console.log(err));
			},
			follow(item){
				// 关注
   				axios.get(api.doFollow()+'&to_user_id='+item.id+'&room_id='+item.room_id)
			      	.then(res => {
			      		let data = res.data;
			      		if(data.status == 1){
			      			// Toast(data.error || '关注成功');
			      			item.has_focus = 1;
			      		}
			      		else{
			      			Toast(data.error || '关注失败');
			      		}
			        	
			      	})
			      	.catch(err => console.log(err))
			},
			viewUser(item){
				if(item.live_in == 1){
					// 加入房间
					let json = {
						"createrId" : item.id,
						"groupId" : item.group_id,
						"roomId" : item.room_id,
						"loadingVideoImageUrl" : item.live_image,
						"create_type" : item.create_type,
						"videoType" : item.video_type
					}
			        json = JSON.stringify(json);
					try{
				  		App.join_live(json);
				  	}
				  	catch(e){
				  		Toast("SDK调用失败");
				  	}
				}
				else{
					// 查看用户资料
					let json = {
						'android_page' : 'com.fanwe.live.activity.LiveUserHomeActivity',
						'ios_page' : 'personalHomepageViewController',
						'data': {
							'user_id' : item.id,
							'nick_name' : item.nick_name
						}
					}
			        json = JSON.stringify(json);
			        try{
				  		App.start_app_page(json);
				  	}
				  	catch(e){
				  		Toast("SDK调用失败");
				  	}
				}
			}
		}
	}
</script>
<style>
	body{
		background-color: #fff;
	}
</style>
<style lang="less" scoped>
	@import "../assets/less/variable.less";
	.state-hide{
		display: none !important;
	}
	.prehour-list{
		padding-left: 0.5rem;
		color: #171717;
		line-height: 1;
		background: #fff;
		.list-list{
			padding: 0.9rem 0.75rem;
			padding-left: 0;
			position: relative;
			display: flex;
		    justify-content: space-between;
		    align-items: center;
		    &:after{
		    	content: '';
			    position: absolute;
			    left: 0;
			    bottom: 0;
			    right: auto;
			    top: auto;
			    height: 1px;
			    width: 200%;
			    background-color: @bdcolor;
			    display: block;
			    z-index: 15;
			    transform-origin: 0 0;
				transform: scale(0.5, 0.5);
				box-sizing: border-box;
		    }
		    .rank-numb{
		    	width: 1.6rem;
		    	height: 1.6rem;
			    flex-shrink: 0;
			    font-size: 1.7rem;
			    display: flex;
    			justify-content: center;
    			color: #a8a8a8;
    			line-height:1.5rem;
    			-webkit-transform:scale(0.5);
				-moz-transform:scale(0.5);
				margin-left: -0.5rem;
		    }
		    .rank-one{
		    	font-size: 1.1rem;
		    	background:#ffd500;
		    	color: #fff;
		    	border-radius: 50px; 
		    }
		    .rank-two{
		    	font-size: 1.1rem;
		    	background:#d1d1d1;
		    	color: #fff;
		    	border-radius: 50px; 
		    }
		    .rank-three{
		    	font-size: 1.1rem;
		    	background:#cdac8c;
		    	color: #fff;
		    	border-radius: 50px;
		    }
		    .rank-color{
		    	color: #171717;
		    }
		    .anchor-img{
		    		width: 2rem;
		    		height: 2rem;
		    		margin-right: 0.5rem;
		    		flex-shrink:0;
		    		position: relative;
		    		.anchor-image{
		    			width: 100%;
		    			height: 100%;
		    			border-radius: 50px;
		    			z-index: 1;
		    			position: relative;
		    		}
		    		.rank-icon{
	    				position: absolute;
					    top: -0.35rem;
					    width: 1rem;
					    height: 1rem;
					    right: -0.35rem;
					    z-index: 0;
			    		.rank-icon-img{
			    			width: 100%;
			    			height: 100%;
			    		}
		    		}
		    	}
		    .anchor{
		    	width: 100%;
		    	display: flex;
		    	overflow: hidden;
		    	
		    	.anchor-info{
		    		width:100%;
		    		line-height: 1.5;
		    		overflow: hidden;
		    		.anchor-name{
		    			font-size: 0.75rem;
		    			display: block;
		    			white-space:nowrap; 
		    			overflow:hidden; 
		    			text-overflow:ellipsis;
		    		}
		    		.hot-numb{
		    			font-size: 0.6rem;
		    			color: #b8b8b8;
		    			display: block;
		    			white-space:nowrap; 
		    			overflow:hidden; 
		    			text-overflow:ellipsis;
		    		}
		    	}
		    }
		    .state{
		    	flex-shrink:0;
		    	margin-left: 2rem;
		    	.state-live{
		    		display: inline-block;
		    		background: @color-theme;
		    		color: #fff;
		    		padding: 0 0.5rem;
		    		border-radius: 50px;
		    		line-height: 1.6rem;
		    		font-size: 1rem;
		    		letter-spacing:2px;
					-webkit-transform:scale(0.5);
					-moz-transform:scale(0.5);
					margin:-0.35rem -0.85rem; 
		    	}
		    	.state-follow{
		    		display: inline-block;
		    		font-size: 1.3rem;
		    		color: @color-theme;
		    		border-radius: 50px;
		    		border: 1px solid @color-theme;
		    		padding: 0 1.5rem;
		    		line-height: 2.7rem;
		    		-webkit-transform:scale(0.5);
					-moz-transform:scale(0.5);
					margin:-0.7rem -1.35rem; 
		    	}
		    	.state-concern{
		    		color: #999;
		    		font-size: 0.65rem;
		    	}
		    }
		    &:last-child{
		    	border-bottom: none;
		    }
		}
	}
</style>


